import React , {Component} from 'react'
import {store} from './reducer';


function action(){
    return {
        type: 'FILTER-ACTION' ,
        model: 'bmv' ,
        year: 2009,
        typeFuel: 'benzin'
    }
}


store.dispatch(action())



export default class Actions extends Component  {

    state = {
        type: 'FILTER-ACTION' ,
        model: 'bmv' ,
        year: 2009,
        typeFuel: 'benzin'
    }

    handleClick(event){
        let value = event.target.value.split("-")
        let keyForState = value[0].toString()
        let valueForState = value[1]
        this.setState({
        type: 'FILTER-ACTION' ,
        [keyForState]:valueForState
        }, () => {
        store.dispatch(this.state);
        })
   }
   checkHandler(e){
       console.log(e.target.checked)
       if (e.target.checked ) { 
        this.setState({
            type:'SEE-ALL-CARS'
            }, () => {
            store.dispatch(this.state);
            })
       } else if (e.target.checked === false) {
        this.setState({
            type:'FILTER-ACTION'
            }, () => {
            store.dispatch(this.state);
            })
       }
       
   }



    render () {

        return (

        <div>
        <label >
        click to see all cars :
        <input
        name="see-all-cars"
        type="checkbox"
        onChange={(e)=>this.checkHandler(e)} />
        </label>
        <div style={{ display: "block"}}  onChange={(e) => this.handleClick(e)}>
        <div>
            click on model:
            <select >
            <option value="model-bmv">BMV</option>
            <option value="model-audi">Audi</option>
            </select>
        </div>
            <div>
            click on year of machine:
            <select   >
            <option value="year-2009">2009</option>
            <option value="year-2000">2000</option>
            </select>
            </div>
            <div>
            click on fuel:
            <select  >
            <option value="typeFuel-benzin">benzin</option>
            <option value="typeFuel-diesel">diesel</option>
            </select>
            </div>
            </div>
            </div>

    )
    }

}



