import React from 'react';







export default function Filtered({obj}) {
console.log(obj)
if ( obj.newObj.length > 0 ){
    return (
        <div>
            <h3>List of filtered obj :</h3>
            
            { 
                 
                obj.newObj.map((el,i,arr)=>{
                    return (
                        <ul key={i}>
                       <li>Machine model : {el.model}</li>
                       <li>Machine year : {el.year}</li>
                       <li>Machine type of engine : {el.typeFuel}</li>
                       </ul>
                    )})
                    
                    
                    }
           
        </div>
    ) 

}else {
    return <h4>no match machines , please change search options</h4>
}
   
    }





