import { createStore } from 'redux'
import React from 'react'

let obj = {
    state:[
        {
            model: "bmv",
            year: 2000,
            typeFuel: "diesel"
          },
          {
            model: "bmv",
            year: 2001,
            typeFuel: "benzin"
          },
          {
            model: "bmv",
            year: 2003,
            typeFuel: "diesel"
          },
          {
            model: "bmv",
            year: 2006,
            typeFuel: "benzin"
          },
          {
            model: "bmv",
            year: 1980,
            typeFuel: "diesel"
          },
          {
            model: "bmv",
            year: 1999,
            typeFuel: "benzin"
          },
          {
            model: "audi",
            year: 2001,
            typeFuel: "diesel"
          },
          {
            model: "audi",
            year: 2003,
            typeFuel: "diesel"
          },
          {
            model: "audi",
            year: 2007,
            typeFuel: "benzin"
          },
          {
            model: "audi",
            year: 1989,
            typeFuel: "diesel"
          },
          {
            model: "audi",
            year: 1989,
            typeFuel: "benzin"
          },
          {
            model: "audi",
            year: 1993,
            typeFuel: "diesel"
          }
    ],
    newObj:[]
}

  ;



function counter(state = obj, action) {
    switch (action.type) {
      case 'FILTER-ACTION':
          const {model, year, typeFuel} = action
          console.log(action)
             function filter(model, year, type) {
              return  state.state.filter(el => {
                    return el.model === model;
                  }).filter(el => {
                    return el.year < year;
                  }).filter(el => {
                    return el.typeFuel === type;
                  });
              
             
              }
              let newObj = filter(model, year, typeFuel)

             return state = {
                 state:state.state,
                 newObj:newObj
             }
      case 'SEE-ALL-CARS':
        console.log(state = {
          state:state.state,
          newObj:state.state
      })
          return state = {
            state:state.state,
            newObj:state.state
        }
      default:
        return state
    }
  }
  
  let store = createStore(counter)
  


 function Reducer() {
    return (
        <div>
            232323
        </div>
    )
}


export   {Reducer,store}


